body {
  margin: 0;
  padding: 0;
  background-color: #1B998B !important;
}

#footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #333A40;
  height: 250px;
  width: 100vw; 
  color: #FAFAFF;
  font-size: 1.25em;
  box-shadow: 0px 0px 75px 0px rgba(25,29,50,0.75);
}

#footerContact {
  display: flex;
  flex-direction: column;
  border-left: 2px solid rgba(250,250,255, 0.5);
  padding: 0 150px;
}

#footerContact a {
  color: #d8d8dd;
  text-decoration: none;
}

#footerContact a:hover {
  color: #FAFAFF;
  text-decoration: underline;
}

@media only screen and (max-width: 1200px) {
  #footerContact {
    padding: 0 50px;
  }
}


@media only screen and (max-width: 800px) {
  #footerContact {
    padding: 0 25px;
  }
}

.changeAmount {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px;
  width: 50px;
  border: 1px solid #aaa;
  cursor: pointer;
}

.increaseAmount {
  border-radius: 10px 10px 0 0;
  /* border-bottom: 1px solid #ddd; */
  border-bottom: none;
}

.decreaseAmount {
  border-radius: 0 0 10px 10px;
  /* border-top: 1px solid #ddd; */
  border-top: none; 
}

.increaseAmount:hover {
  color: #fafaff;
  background-color: #1B998B;
}

.decreaseAmount:hover {
  color: #fafaff;
  background-color: #eb6565;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: #fafaff;
  padding-top: 50px;
}

#bgImage {
  height: 500px;
  width: 100vw;
  background-color: #F9F8F6;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  -webkit-filter: blur(4px);
          filter: blur(4px);
  background-image: url(/sportcompass-application/static/media/Robots_illustration.6251a8dc.jpg);
  position: absolute;
}

#productPageHeader {
  display: flex;
  justify-content: center;
}

#productPageHeaderTitle {
  margin-top: 125px;
  padding: 10px;
  position: absolute;
  background-color: #1B998B;
  opacity: 0.8;
  border-radius: 25px;
  box-shadow: 0px 0px 250px 0px rgba(51,58,64,1);
}

@media only screen and (min-width: 1500px) {
  .customContainer {
    width: 1500px;
  }
}

